body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:linear-gradient(86deg, #303638 0%, #47565c 100%)}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.App{text-align:center;font-family:"Quicksand"}
nav{display:flex;justify-content:space-between;align-items:center;box-shadow:0 5px 10px #171818;padding:7px 2%;background-color:rgba(0,0,0,.3)}nav p{font-size:130%;text-decoration:underline;cursor:pointer;padding:8px;color:#3fafa6;background:linear-gradient(to bottom, rgba(23, 24, 24, 0.7) 0%, rgba(0, 0, 0, 0.15) 100%);border:solid 2px #3fafa6;border-radius:15px;box-shadow:0 0 10px #389991}nav p:hover{color:#389991;background:linear-gradient(to bottom, rgba(23, 24, 24, 0.7) 0%, rgba(0, 0, 0, 0.25) 100%);border-color:#389991}
.logo-box .tilt{width:80px;height:80px;border:solid 2px #3fafa6;box-shadow:0 0 8px #3fafa6;background:linear-gradient(to bottom, rgba(23, 24, 24, 0.7) 0%, rgba(0, 0, 0, 0.15) 100%)}.logo-box .tilt .logo-svg{padding-top:40px;fill:#3fafa6}
.form-box{width:auto;padding:0 2%}.form-box p{font-size:140%;color:#389991;font-weight:500;color:#1f2122}.form-box .form-input{display:flex;justify-content:center}.form-box input{width:70%;padding:5px;font-size:130%;background:linear-gradient(to bottom, rgba(34, 36, 36, 0.7) 0%, rgba(0, 0, 0, 0.15) 100%)}.form-box button{width:30%;cursor:pointer;font-size:130%;padding:5px;background:linear-gradient(to bottom, rgba(23, 24, 24, 0.7) 0%, rgba(0, 0, 0, 0.15) 100%);border:solid 2px #3fafa6;color:#3fafa6}.form-box button:hover{color:#389991;border-color:#389991;background:linear-gradient(to bottom, rgba(24, 26, 26, 0.7) 0%, rgba(0, 0, 0, 0.25) 100%)}
.rank-box{width:35%;margin:40px auto;padding:10px}.rank-box .rank-text{font-size:140%;color:#3fafa6}.rank-box .rank-num{font-size:200%;color:#3fafa6;text-shadow:0 0 4px #389991}

